@import url(https://fonts.googleapis.com/css2?family=Work+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Work+Sans&display=swap);
.createPost{
    display: flex;
    justify-content: center;
    
}

/* .image_upload_post > input{
    /* display: none; */

.post_btn{
    border: none;
    background-color: white;
    cursor: pointer;
}



.post_bottom{
    display: flex;
    justify-content: space-between;
}

.post{
    background-color: white;
    width: 100%;
    max-width: 650px;
    margin-top: 7vh;
    padding: 12px 16px;
    font-family: 'Work Sans', sans-serif;
    border-radius: 4px;
    
}

@media only screen and (max-width: 600px){
    .post{
        width: 90%;
    }
}
.textarea{
    border: none;
    width:90%;
    outline: none;
    font-family: 'Work Sans', sans-serif;
    font-size: 15px;
    
}

.textarea :focus{
    outline: none;
}

#image_preview{
    display: none;
    max-width: 400px;
    max-height: 200px;
    border-radius: 4px;
    margin-bottom: 3vh;

}

.heading_bar{
    display: flex;
    justify-content: space-between;
    padding-left: 4vh ;
    padding-right: 4vh;
    background-color: rgb(255, 255, 255);
    align-items: center;
    color: crimson;
}
.signin_btn{
    margin-left: auto;
    height: 5vh;
    color: rgb(255, 255, 255);
    /* background-image: linear-gradient(#ff8a00, #e52e71); */
    background-image: linear-gradient(to right,#0072ff,#00d4ff);


    border-radius: 0.3rem;
    border: none;
    cursor: pointer;
    transition: background-color .5s;
    
}

.signin_btn:hover{
    /* background-image: linear-gradient(45deg, #b43f41, #ffdd33);; */
    background-image: linear-gradient(#ff8a00, #e52e71);
    /* background-image: linear-gradient(to right,#0072ff,#00d4ff); */


    color: rgb(255, 255, 255);
    
}

.signin_btn:focus{
    outline: none;
}

.user_img{
    border:2px solid rgb(0, 0, 0);
    border-radius: 2rem;
    height: 5vh;
    padding: 0.3vh;
}

.heading_h1 {
    background-color: #FF4E50;
    /* background-image: linear-gradient(45deg, #b43f41, #ffdd33); */
    background-image: linear-gradient(#ff8a00, #e52e71);
    background-image: linear-gradient(to right,#0072ff,#00d4ff);

    background-size: 100%;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-text-fill-color: transparent;
    
}
.footer {
    display: flex;
    justify-content: center;
    margin-top: 3vh;
    margin-bottom: 2vh;
    color: rgb(20, 126, 168);
}

.post{
    display: flex;
    flex-direction: column;
}

.user_img{
    border:2px solid rgb(0, 0, 0);
    border-radius: 2rem;
    height: 5vh;
    padding: 0.3vh;
}

.post_delete_btn{
    border: none;
    cursor: pointer;
    color: crimson;
    background-color: white;
    margin-top: 5vh;

}
.post_delete_btn:focus{
    outline: none;
}
.post_delete_btn:hover{
    color: rgb(0, 128, 255);
}

.post_img{
    width:100%;

}
.home_feed{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin-bottom: 0px;
}
body{
    background-color: rgb(235, 235, 235);
    margin: 0;
    font-family: 'Work Sans', sans-serif;

}
